* {
  font-family: 'Roboto', sans-serif;
}

body {
  color:white;
  background-color: #1a1a1a;
  padding: 60px;
}


input {
  color: black;
  font-size: 1.2rem;
  width: 85%;
  border-radius: 25px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 170px;
}

